.promo {
  margin: 0 auto;
  padding: 120px 0;

  @include vp-1199 {
    padding: 100px 0;
  }

  @include vp-767 {
    padding: 70px 0;
  }
}
.promo__wrapper {
  width: $desktop-content;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 500px 660px;
  justify-content: center;

  @include vp-1199 {
    width: auto;
    margin: 0 $tablet-margin-asade;
    grid-template-columns: 292px 386px;
  }

  @include vp-767 {
    margin: 0 $mobile-margin-asade;
    display: block;
  }

  img {
    display: block;
    width: 660px;
    height: 500px;
    background-color: $color-sapphire-blue;

    @include vp-1199 {
      width: auto;
      width: 386px;
      height: 292px;
    }

    @include vp-767 {
      width: 100%;
      height: 292px;
      object-fit: cover;
    }
  }
}

.promo__information-row-wrapper {
  display: flex;
}

.promo__information-date-time-wrapper {
  margin-left: 50px;

  @include vp-1199 {
    margin-left: 30px;
  }

  @include vp-767 {
    margin-left: 29px;
  }

  h3 {
    font-weight: 400;
    font-size: 18px;
    line-height: 100%;
    color: $color-default-white;
    margin: 0;
    width: fit-content;

    @include vp-1199 {
      font-size: 12px;
    }
  }

  p {
    font-weight: 700;
    font-size: 40px;
    line-height: 100%;
    color: $color-default-white;
    margin: 0;
    display: flex;
    align-items: center;
    margin-top: 10px;
    width: fit-content;
    position: relative;

    &::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 2px;
      background-color: $color-default-white;
      top: -5px;
      left: 0;

      @include vp-1199 {
        top: -2px;
      }
    }


    @include vp-1199 {
      font-size: 24px;
      margin-top: 5px;
    }
  }

  span {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: $color-default-white;
    margin-left: 7px;

    @include vp-1199 {
      font-size: 10px;
      margin-left: 4px;
    }
  }
}

.promo__information-wrapper {
  display: flex;
  flex-direction: column;
  background-color: $color-torch-red;
  padding: 87px 78px 85px 78px;

  @include vp-1199 {
    padding: 51px 45px 49px 45px;
  }

  @include vp-767 {
    display: block;
    padding: 51px 43px 51px 43px;
  }

  h2 {
    font-family: $ff-bebas-neue;
    font-weight: 700;
    font-size: 90px;
    line-height: 90px;
    letter-spacing: 0.03em;
    color: $color-default-white;
    margin: 0;

    @include vp-1199 {
      font-size: 52px;
      line-height: 52px;
    }
  }

   h4 {
    font-size: 18px;
    line-height: 150%;
    color: $color-default-white;
    margin: 0;
    font-weight: 400;
    margin-top: 3px;

    @include vp-1199 {
      font-size: 12px;
      margin-top: -1px;
    }
  }
}

.promo__information-time {
  margin-top: 23px;

  @include vp-1199 {
    margin-top: 7px;
  }

  p {
    margin-top: 13px;
  }
}

.promo__button {
  box-sizing: border-box;
  display: block;
  width: 343px;
  padding: 25px;
  border: 4px solid $color-default-white;
  text-align: center;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-default-white;
  margin-bottom: 0;
  margin-top: auto;

  @include vp-1199 {
    width: 204px;
    padding: 12px;
    font-size: 10px;
    line-height: 12px;
    border: 2px solid $color-default-white;
  }

  @include vp-767 {
    width: 100%;
    margin-top: 29px;
  }
}
